export default {
	data() {
		return {
			transform: 'translateY(50vh)',
			timer: 0,
			backgroundColor: 'rgba(0,0,0,0)',
			show: false,
			config: {},
		};
	},
	props:{
		contentHeight:{
			type: Number,
			default: 0
		},
		//是否是tabbar页面
		hasTabbar:{
			type: Boolean,
			default: false
		},
		dataurl:{
			type: String,
			default: ''
		},
		shareList:{
			type: Array,
			default: function(){
				return [];
			}
		}
	},
	created() {
		const height = (uni.upx2px(this.contentHeight)-100) + 'px';
		this.config = {
			height: height,
			transform: `translateY(${height})`,
			backgroundColor: 'rgba(0,0,0,.4)',
		}
		this.transform = this.config.transform;
	},
	methods:{
		toggleMask(){
			//防止高频点击
			if(this.timer == 1){
				return;
			}
			this.timer = 1;
			setTimeout(()=>{
				this.timer = 0;
			}, 500)
			
			if(this.show){
				this.transform = this.config.transform;
				this.backgroundColor = 'rgba(0,0,0,0)';
				setTimeout(()=>{
					this.show = false;
					this.hasTabbar && uni.showTabBar();
				}, 200)
				return;
			}
			
			this.show = true;
			//等待mask重绘完成执行
			if(this.hasTabbar){
				uni.hideTabBar({
					success: () => {
						setTimeout(()=>{
							this.backgroundColor = this.config.backgroundColor;
							this.transform = 'translateY(0px)';
						}, 10)
					}
				});
			}else{
				setTimeout(()=>{
					this.backgroundColor = this.config.backgroundColor;
					this.transform = 'translateY(0px)';
				}, 10)
			}
		},
		//防止冒泡和滚动穿透
		stopPrevent(){},
		//分享操作
		shareToFriend(type){
			// this.$api.msg(`分享给${type}`);
			if(type==1){ //微信朋友圈
				
			}else if(type==2){ //微信好友
				
			}else if(type==3){ //产品二维码
				
			}else if(type==4){ //复制链接

				uni.setClipboardData({
				    data: uni.getStorageSync('hostname')+'/h5/#'+this.dataurl,
				    success: function () {
				        console.log('success');
				    }
				});

			}
			this.toggleMask();
		},
	}
}
